* {
    margin: 0;
    padding: 0;
}

::-webkit-scrollbar {
    display: none; /* Chrome Safari */
}

.layout {
    display: flex;
    flex-direction: row;
    justify-content: flex-start;
    background-color: #F5F5FB;
}

.middle {
    display: flex;
    flex-direction: column;
    width: 81%;
}