.analysis {
  width: 100%;
  margin-top: 90px;
  .container {
    display: flex;
    padding: 50px 50px 0 50px;
    gap: 50px;
    .left {
      display: flex;
      flex-direction: column;
      justify-content: flex-start;
      align-items: flex-start;
      .left-upper {
        display: flex;
        margin-left: 20px;
        justify-content: flex-start;
        gap: 100px;
        align-items: center;
        .img1 {
          width: 360px;
          height: 320px;
          background-color: #fff;
          border-radius: 50px;
          display: flex;
          justify-content: center;
          align-items: center;
        }
        .img2 {
          display: flex;
          align-items: center;
          justify-content: center;
          width: 360px;
          height: 320px;
          background-color: #fff;
          border-radius: 50px;
        }
      }
      .left-lower {
        display: flex;
        justify-content: flex-start;
        gap: 20px;
        align-items: center;
      }
    }
    .right {
      display: flex;
      justify-content: center;
      align-items: center;
      background-color: #fff;
      border-radius: 50px;
      padding: 0 30px 0 20px;
    }
  }
}