.visualization {
  width: 100%;
  margin-top: 90px;
  .container {
    width: 100%;
    display: flex;
    flex-direction: column;
    gap: 15px;
    .result-block {
      padding: 20px 200px 20px 50px;
      display: flex;
      flex-direction: column;
      gap: 20px;
      .result-upper {
        display: flex;
        justify-content: space-between;
        align-items: center;
        .result-name {
          font-weight: 700;
          font-size: 16px;
        }
        .result-btn {
          display: flex;
          gap: 10px;
        }
      }
      .result-lower {

      }
    }
  }
}