.navbar {
  height: 90px;
  position: fixed;
  width: 81%;
  background-color: #F5F5FB;;
  z-index: 99;
  display: flex;
  align-items: center;
  justify-content: flex-start;
  .container-nav {
    display: flex;
    padding: 24px 20px 10px 40px;
    flex-direction: row;
    justify-content: flex-start;
    align-items: center;
    .left {
      margin-right: 160px;
    }
    .right {
      color: #768492
    }
  }
}


